<template>
  <div>
    <el-tabs
      :value="activeName"
      @tab-click="handleClick"
      class="routerWraper"
      :before-leave="beforeLeave"
    >
      <el-tab-pane v-for="(item,index) in routerData" :key="index" :label="item" :name="item"></el-tab-pane>
    </el-tabs>

    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>
<script>
import { mapState, mapGetters } from "vuex";

export default {
  name: "customer",
  data() {
    return {
      activeRouter: 0,
      routerData: ["CUSTOMER", "SEGMENTATION", "FHC", "RECOMMENDATION"]
      // activeName: "CUSTOMER"
    };
  },
  computed: {
    ...mapState("moduleHead", {
      activeName: state => state.activeName
    })
  },
  created() {},

  watch: {
    $route(to, from) {
      // this.activeName = to.query.active || "CUSTOMER";
      // this.$store.commit("moduleCustomer/changeNav", to.name);
    }
  },

  methods: {
    beforeLeave() {
      return this.$store.state.flag;
    },
    handleClick(tab, event) {
      this.goNext(tab.index * 1);
    },
    goNext(key) {
      this.activeRouter = key;
      switch (key) {
        case 0:
          this.$router
            .push({
              name: "customerinfo"
            })
            .catch(err => {
              err;
            });
          break;
        case 1:
          this.$router
            .push({
              name: "segmentationOptions"
            })
            .catch(err => {
              err;
            });
          break;
        case 2:
          this.$router
            .push({
              name: "fhc"
            })
            .catch(err => {
              err;
            });
          break;
        case 3:
          this.$router
            .push({
              name: "product"
            })
            .catch(err => {
              err;
            });
          break;

        default:
          break;
      }
    }
  }
};
</script>
