<template>
  <div class="customerProfile">
    <div class="customer_main">
      <p class="titleNode">{{ customerInfo }}</p>
      <div class="customerInfo_main">
        <div class="avatar-main">
          <el-avatar :size="100" :src="circleUrl"></el-avatar>
          <span class="username">{{ username }}</span>
        </div>
        <div class="baseInfo">
          <p>
            <label>Name</label>
            <label>{{ baseInfoData.name }}</label>
          </p>
          <p>
            <label>Surname</label>
            <label>{{ baseInfoData.surname }}</label>
          </p>
          <p>
            <label>Gender</label>
            <label>{{ baseInfoData.Gender }}</label>
          </p>
          <p>
            <label>DOB</label>
            <label>{{ baseInfoData.DOB }}</label>
          </p>
          <p>
            <label>Mobile No</label>
            <label>{{ baseInfoData.mobileNO }}</label>
          </p>
          <p>
            <label>Occupation</label>
            <label>{{ baseInfoData.occupation }}</label>
          </p>
          <p>
            <label>Email</label>
            <label>{{ baseInfoData.email }}</label>
          </p>
          <p>
            <label>Greated</label>
            <label>{{ baseInfoData.greated }}</label>
          </p>
          <p>
            <label>Last Updated</label>
            <label>{{ baseInfoData.lastUpdated }}</label>
          </p>
        </div>
      </div>
    </div>
    <div class="customer_main">
      <p class="titleNode">{{ statusNode }}</p>
      <el-steps :active="activeStatus" finish-status="success" align-center>
        <el-step
          v-for="(item,index) in stepItems"
          :key="index"
          :title="item.title"
          icon="el-icon-warning"
          @click.native="stepClick(index,item.title)"
        ></el-step>
      </el-steps>
    </div>
    <div class="remarkMain">
      <p class="titleNode">{{ noteNode }}</p>
      <div class="nodeMain">
        <span class="editButton" v-show="editShow" @click="ifEdit">EDIT</span>
        <span class="editButton" v-show="DONEShow" @click="ifDONE">DONE</span>
        <el-input
          type="textarea"
          placeholder=""
          v-model="note"
          rows="10"
          ref="remarkTexarea"
          @blur="loseBlur"
        ></el-input>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "customerProfile",
  data() {
    return {
      customerInfo: "INFORMATION",
      statusNode: "STATUS",
      noteNode: "NOTE",
      editShow: true,
      DONEShow: false,
      circleUrl:
        "https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png",
      username: "Lucia Gao",
      baseInfoData: {
        name: "Grace",
        surname: "Hao",
        Gender: "Female",
        DOB: "DD/MM/YYYY",
        mobileNO: "123456",
        occupation: "manager",
        email: "aia@aia.com",
        greated: "DD/MM/YYYY",
        lastUpdated: "DD/MM/YYYY"
      },
      activeStatus: 1,
      stepItems: [
        { title: "CUSTOMER INFO" },
        { title: "SEGMENTATION" },
        { title: "FHC" },
        { title: "PRODUCT RECOMMENDATION" }
      ],
      note: ""
    };
  },
  methods: {
    stepClick(index, item) {
      if (index <= this.activeStatus) {
        if (item == "CUSTOMER INFO") {
          this.$router.push({
            name: "customerinfo"
          });
        } else if (item == "SEGMENTATION") {
          this.$router.push({
            name: "segmentationOptions"
          });
        } else if (item == "FHC") {
          this.$router.push({
            name: "fhc"
          });
        }
      }
    },
    ifEdit() {
      this.editShow = !this.editShow;
      this.DONEShow = !this.DONEShow;
      this.$nextTick(function() {
        this.$refs.remarkTexarea.focus();
      });
    },
    ifDONE() {
      this.editShow = !this.editShow;
      this.DONEShow = !this.DONEShow;
      this.$nextTick(function() {
        this.$refs.remarkTexarea.blur();
      });
    },
    loseBlur() {
      this.editShow = true;
      this.DONEShow = false;
    }
  }
};
</script>

