<template>
  <div style="width:100%;height:100%">
    <slot></slot>
    <div class="parCardCol">
      <aia-card
        @checkItem="checkFun(index)"
        v-for="(item,index) in subArr"
        :key="index"
        :keyIndex="index"
        :title="item.title"
        :imgurl="item.img"
        :isCheck="checkIndex.includes(index)"
      ></aia-card>
    </div>
  </div>
</template>
<script>
import AiaCard from "./AiaCard";

export default {
  name: "CrpCard",
  props: ["subArr", "card", "type"],
  components: {
    AiaCard
  },
  data() {
    return {
      checkIndex: []
    };
  },
  methods: {
    checkFun(ind) {
      if (this.card == 1) {
        if (this.checkIndex.includes(ind)) {
          this.checkIndex.splice(ind, 1, null);
        } else {
          if (this.checkIndex.length == 0) {
            if (ind == 0) {
              this.checkIndex.push(ind);
            } else {
              this.checkIndex.push(null, ind);
            }
          } else {
            this.checkIndex.splice(ind, 1, ind);
          }
        }
        // console.log(this.checkIndex);
      } else {
        this.checkIndex = [];
        this.checkIndex.push(ind);
      }
      console.log(this.checkIndex, "....");

      this.$emit("checkIndexkey", {
        type: this.type,
        checkIndex: this.checkIndex
      });
    }
  }
};
</script>
<style lang="scss">
.parCardCol {
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>